import React, { Component } from 'react';
import { Redirect } from 'react-router';
import Header from '../components/Header';
import Loading from '../components/Loading';
import { getUser, updateUser } from '../services/userAPI';
import '../css/profileEdit.css'
import { FaCheck, FaTimes } from 'react-icons/fa';

class ProfileEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      description: '',
      image: '',
      loading: false,
      saved: false,
    };
  }

  componentDidMount() {
    this.getUserInformation();
  }

  getUserInformation = async () => {
    const { name, email, description, image } = await getUser();
    this.setState({ name, email, description, image });
  }

  handleChange = ({ target: { name, value } }) => {
    this.setState({ [name]: value });
  }

  onButtonClick = async () => {
    const { name, email, description, image } = this.state;
    const obj = { name, email, description, image };
    this.setState({ loading: true });
    await updateUser(obj);
    this.setState({ loading: false, saved: true });
  }

  form() {
    const { name, email, description, image } = this.state;
    const regex = /(.*)@\w*[.][\w]{2,3}$/gm;
    let disabled = true;
    let emailCheck = email.match(regex);
    if (name && description && image && emailCheck) {
      disabled = false;
    }
    let hold = 'data:image/png;base64,'
    if (image) {
      hold = image
    }
    return (
      <div className="page-profile-edit">
        <form>
          <div className="image-container">
            <img src={ hold } alt="user" />
            <input
              placeholder="Digite um link..."
              name="image"
              id="edit-input-image"
              value={ image }
              onChange={ this.handleChange }
            />
            { image 
            ? <div className="check"><FaCheck /></div> 
            : <div className="times"><FaTimes /></div>}
          </div>
          
            <h2>Name:</h2>
            <div className="flex">
              <input
                name="name"
                id="edit-input-name"
                placeholder="digite seu nome..."
                value={ name }
                onChange={ this.handleChange }
              />
              { name 
              ? <div className="check"><FaCheck /></div> 
              : <div className="times"><FaTimes /></div>
              }
            </div>
          
          
            <h2>Email:</h2>
            <div className="flex">
              <input
                name="email"
                id="edit-input-email"
                placeholder="digite seu email..."
                value={ email }
                onChange={ this.handleChange }
              />
              { emailCheck
              ? <div className="check"><FaCheck /></div> 
              : <div className="times"><FaTimes /></div>
              }
            </div>
          <label htmlFor="edit-input-description">
            <h2>Description:</h2>
            <div className="flex">
            <textarea
              placeholder="digite uma descrição..."
              name="description"
              id="edit-input-description"
              data-testid="edit-input-description"
              value={ description }
              onChange={ this.handleChange }
            />
              { description
                ? <div className="check"><FaCheck /></div>
                : <div className="times"><FaTimes /></div>
              }
            </div>
          </label>     
          <button
            className="edit-button-save"
            disabled={ disabled }
            type="button"
            onClick={ this.onButtonClick }
          >
            Salvar
          </button>
        </form>
      </div>
    );
  }

  render() {
    const { loading, saved } = this.state;
    return (
      <>
        <Header />
        { loading ? <Loading /> : this.form() }
        { saved && <Redirect to="/trybe-tunes/profile" /> }
      </>
    );
  }
}

export default ProfileEdit;
